/**
 * DocumentViewerPage - Full PDF viewer with evidence highlights
 *
 * Route: /cases/:caseId/documents/:fileId
 * Features:
 * - PDF rendering with react-pdf
 * - Page navigation with keyboard shortcuts
 * - Evidence/quote highlighting (best-effort)
 * - Jump to specific page via query params
 */

import React, { useEffect, useState, useCallback, useMemo } from 'react';
import { useParams, useSearchParams, useNavigate, Link } from 'react-router-dom';
import {
  ChevronRight,
  ChevronLeft,
  ZoomIn,
  ZoomOut,
  RotateCw,
  Download,
  Maximize2,
  X,
  FileText,
  Search,
  Home,
  Loader2,
  AlertCircle,
} from 'lucide-react';
import { PDFViewer } from '../components/documents/PDFViewer';
import { DocumentToolbar } from '../components/documents/DocumentToolbar';
import { DocumentMinimap } from '../components/documents/DocumentMinimap';
import { HighlightOverlay } from '../components/documents/HighlightOverlay';

interface DocumentInfo {
  id: string;
  filename: string;
  mimeType: string;
  pageCount: number;
  url: string;
}

export const DocumentViewerPage: React.FC = () => {
  const { caseId, fileId } = useParams<{ caseId: string; fileId: string }>();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  // State
  const [documentInfo, setDocumentInfo] = useState<DocumentInfo | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [numPages, setNumPages] = useState(0);
  const [scale, setScale] = useState(1.0);
  const [showMinimap, setShowMinimap] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [isSearching, setIsSearching] = useState(false);

  // Get initial page and quote from URL params
  const initialPage = parseInt(searchParams.get('page') || '1', 10);
  const highlightQuote = searchParams.get('quote') || '';

  // Fetch document info
  useEffect(() => {
    const fetchDocument = async () => {
      if (!fileId) return;

      setLoading(true);
      setError(null);

      try {
        // Try to get document metadata
        const response = await fetch(`/api/files/${fileId}/info`);
        if (!response.ok) {
          // Fallback: assume it's a valid document
          setDocumentInfo({
            id: fileId,
            filename: `document-${fileId}.pdf`,
            mimeType: 'application/pdf',
            pageCount: 0, // Will be updated when PDF loads
            url: `/api/files/${fileId}/content`,
          });
        } else {
          const data = await response.json();
          setDocumentInfo({
            id: fileId,
            filename: data.filename || data.name || `document-${fileId}.pdf`,
            mimeType: data.mime_type || data.mimeType || 'application/pdf',
            pageCount: data.page_count || data.pages || 0,
            url: data.url || `/api/files/${fileId}/content`,
          });
        }
      } catch (err) {
        console.error('Error fetching document:', err);
        // Still try to show the document
        setDocumentInfo({
          id: fileId,
          filename: `document-${fileId}.pdf`,
          mimeType: 'application/pdf',
          pageCount: 0,
          url: `/api/files/${fileId}/content`,
        });
      } finally {
        setLoading(false);
      }
    };

    fetchDocument();
  }, [fileId]);

  // Set initial page from URL
  useEffect(() => {
    if (initialPage > 0) {
      setCurrentPage(initialPage);
    }
  }, [initialPage]);

  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      // Don't handle if user is typing in an input
      if (
        e.target instanceof HTMLInputElement ||
        e.target instanceof HTMLTextAreaElement
      ) {
        return;
      }

      switch (e.key) {
        case 'n':
        case 'ArrowRight':
        case 'PageDown':
          e.preventDefault();
          setCurrentPage((p) => Math.min(p + 1, numPages));
          break;
        case 'p':
        case 'ArrowLeft':
        case 'PageUp':
          e.preventDefault();
          setCurrentPage((p) => Math.max(p - 1, 1));
          break;
        case 'f':
          if (e.ctrlKey || e.metaKey) {
            e.preventDefault();
            setIsSearching(true);
          }
          break;
        case 'Escape':
          if (isSearching) {
            setIsSearching(false);
            setSearchQuery('');
          } else {
            navigate(`/cases/${caseId}`);
          }
          break;
        case '+':
        case '=':
          if (e.ctrlKey || e.metaKey) {
            e.preventDefault();
            setScale((s) => Math.min(s + 0.1, 3.0));
          }
          break;
        case '-':
          if (e.ctrlKey || e.metaKey) {
            e.preventDefault();
            setScale((s) => Math.max(s - 0.1, 0.5));
          }
          break;
        case 'Home':
          e.preventDefault();
          setCurrentPage(1);
          break;
        case 'End':
          e.preventDefault();
          setCurrentPage(numPages);
          break;
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [numPages, isSearching, navigate, caseId]);

  const handleDocumentLoad = useCallback((pages: number) => {
    setNumPages(pages);
    setLoading(false);
  }, []);

  const handlePageChange = useCallback((page: number) => {
    setCurrentPage(page);
  }, []);

  const handleZoomIn = () => setScale((s) => Math.min(s + 0.1, 3.0));
  const handleZoomOut = () => setScale((s) => Math.max(s - 0.1, 0.5));
  const handleZoomReset = () => setScale(1.0);

  const handleDownload = () => {
    if (documentInfo?.url) {
      window.open(documentInfo.url, '_blank');
    }
  };

  const handleClose = () => {
    navigate(`/cases/${caseId}`);
  };

  if (loading && !documentInfo) {
    return (
      <div className="h-screen flex items-center justify-center bg-slate-900">
        <div className="text-center">
          <Loader2 className="w-8 h-8 text-blue-500 animate-spin mx-auto" />
          <p className="text-white/70 mt-4">טוען מסמך...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="h-screen flex items-center justify-center bg-slate-900">
        <div className="text-center max-w-md">
          <AlertCircle className="w-12 h-12 text-red-500 mx-auto" />
          <h2 className="text-white text-xl mt-4">שגיאה בטעינת המסמך</h2>
          <p className="text-white/60 mt-2">{error}</p>
          <button
            onClick={handleClose}
            className="mt-6 px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg"
          >
            חזור לתיק
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="h-screen flex flex-col bg-slate-900" dir="rtl">
      {/* Header */}
      <header className="flex items-center justify-between px-4 py-3 bg-slate-800 border-b border-slate-700">
        {/* Left: Breadcrumb */}
        <div className="flex items-center gap-3">
          <Link
            to={`/cases/${caseId}`}
            className="p-2 hover:bg-slate-700 rounded-lg transition-colors"
            title="חזור לתיק"
          >
            <Home className="w-5 h-5 text-white/70" />
          </Link>
          <div className="h-6 w-px bg-slate-700" />
          <div className="flex items-center gap-2 text-sm">
            <FileText className="w-4 h-4 text-blue-400" />
            <span className="text-white font-medium truncate max-w-[300px]">
              {documentInfo?.filename || 'מסמך'}
            </span>
          </div>
        </div>

        {/* Center: Page info */}
        <div className="flex items-center gap-4">
          <button
            onClick={() => setCurrentPage((p) => Math.max(p - 1, 1))}
            disabled={currentPage <= 1}
            className="p-1.5 hover:bg-slate-700 rounded disabled:opacity-40 disabled:cursor-not-allowed"
            title="עמוד קודם (P)"
          >
            <ChevronRight className="w-5 h-5 text-white/70" />
          </button>
          <div className="flex items-center gap-2 text-white/80 text-sm">
            <input
              type="number"
              value={currentPage}
              onChange={(e) => {
                const page = parseInt(e.target.value, 10);
                if (page >= 1 && page <= numPages) {
                  setCurrentPage(page);
                }
              }}
              className="w-12 px-2 py-1 bg-slate-700 border border-slate-600 rounded text-center text-white"
              min={1}
              max={numPages}
            />
            <span>/</span>
            <span>{numPages || '?'}</span>
          </div>
          <button
            onClick={() => setCurrentPage((p) => Math.min(p + 1, numPages))}
            disabled={currentPage >= numPages}
            className="p-1.5 hover:bg-slate-700 rounded disabled:opacity-40 disabled:cursor-not-allowed"
            title="עמוד הבא (N)"
          >
            <ChevronLeft className="w-5 h-5 text-white/70" />
          </button>
        </div>

        {/* Right: Tools */}
        <div className="flex items-center gap-2">
          {/* Search toggle */}
          <button
            onClick={() => setIsSearching(!isSearching)}
            className={`p-2 rounded-lg transition-colors ${
              isSearching ? 'bg-blue-600 text-white' : 'hover:bg-slate-700 text-white/70'
            }`}
            title="חיפוש (Ctrl+F)"
          >
            <Search className="w-5 h-5" />
          </button>

          <div className="h-6 w-px bg-slate-700" />

          {/* Zoom controls */}
          <button
            onClick={handleZoomOut}
            className="p-2 hover:bg-slate-700 rounded-lg text-white/70"
            title="הקטנה (-)"
          >
            <ZoomOut className="w-5 h-5" />
          </button>
          <button
            onClick={handleZoomReset}
            className="px-2 py-1 hover:bg-slate-700 rounded text-sm text-white/70"
          >
            {Math.round(scale * 100)}%
          </button>
          <button
            onClick={handleZoomIn}
            className="p-2 hover:bg-slate-700 rounded-lg text-white/70"
            title="הגדלה (+)"
          >
            <ZoomIn className="w-5 h-5" />
          </button>

          <div className="h-6 w-px bg-slate-700" />

          {/* Minimap toggle */}
          <button
            onClick={() => setShowMinimap(!showMinimap)}
            className={`p-2 rounded-lg transition-colors ${
              showMinimap ? 'bg-blue-600 text-white' : 'hover:bg-slate-700 text-white/70'
            }`}
            title="מפת ניווט"
          >
            <Maximize2 className="w-5 h-5" />
          </button>

          {/* Download */}
          <button
            onClick={handleDownload}
            className="p-2 hover:bg-slate-700 rounded-lg text-white/70"
            title="הורדה"
          >
            <Download className="w-5 h-5" />
          </button>

          {/* Close */}
          <button
            onClick={handleClose}
            className="p-2 hover:bg-red-600/20 rounded-lg text-red-400"
            title="סגור (Esc)"
          >
            <X className="w-5 h-5" />
          </button>
        </div>
      </header>

      {/* Search bar */}
      {isSearching && (
        <div className="px-4 py-2 bg-slate-800/80 border-b border-slate-700">
          <div className="flex items-center gap-3 max-w-md">
            <Search className="w-4 h-4 text-white/50" />
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="חפש בטקסט..."
              className="flex-1 bg-slate-700 border border-slate-600 rounded-lg px-3 py-1.5 text-sm text-white placeholder:text-white/40 focus:outline-none focus:border-blue-500"
              autoFocus
            />
            <button
              onClick={() => {
                setIsSearching(false);
                setSearchQuery('');
              }}
              className="p-1 hover:bg-slate-700 rounded text-white/60"
            >
              <X className="w-4 h-4" />
            </button>
          </div>
        </div>
      )}

      {/* Main content */}
      <div className="flex-1 flex overflow-hidden">
        {/* PDF Viewer */}
        <div className="flex-1 overflow-auto bg-slate-950">
          {documentInfo && (
            <PDFViewer
              url={documentInfo.url}
              currentPage={currentPage}
              scale={scale}
              onDocumentLoad={handleDocumentLoad}
              onPageChange={handlePageChange}
              highlightQuote={highlightQuote}
              searchQuery={searchQuery}
            />
          )}
        </div>

        {/* Minimap */}
        {showMinimap && numPages > 0 && (
          <DocumentMinimap
            numPages={numPages}
            currentPage={currentPage}
            onPageClick={handlePageChange}
            evidencePages={highlightQuote ? [initialPage] : []}
          />
        )}
      </div>

      {/* Keyboard shortcuts hint */}
      <div className="px-4 py-2 bg-slate-800/50 border-t border-slate-700 text-xs text-white/40 flex items-center justify-center gap-6">
        <span>N/P - עמוד הבא/קודם</span>
        <span>Ctrl+F - חיפוש</span>
        <span>Ctrl+/- - זום</span>
        <span>Esc - סגור</span>
      </div>
    </div>
  );
};

export default DocumentViewerPage;
