/**
 * PDFViewer - PDF rendering component using react-pdf
 *
 * Features:
 * - Single page rendering with zoom
 * - Text layer for selection and search
 * - Annotation layer for highlights
 * - Loading states
 */

import React, { useCallback, useState, useMemo } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import { Loader2, AlertCircle, FileX } from 'lucide-react';
import 'react-pdf/dist/Page/AnnotationLayer.css';
import 'react-pdf/dist/Page/TextLayer.css';

// Set worker path for react-pdf
pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.mjs`;

interface PDFViewerProps {
  url: string;
  currentPage: number;
  scale?: number;
  onDocumentLoad?: (numPages: number) => void;
  onPageChange?: (page: number) => void;
  highlightQuote?: string;
  searchQuery?: string;
}

export const PDFViewer: React.FC<PDFViewerProps> = ({
  url,
  currentPage,
  scale = 1.0,
  onDocumentLoad,
  onPageChange,
  highlightQuote,
  searchQuery,
}) => {
  const [numPages, setNumPages] = useState(0);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [pageLoading, setPageLoading] = useState(true);

  const handleDocumentLoadSuccess = useCallback(
    ({ numPages: pages }: { numPages: number }) => {
      setNumPages(pages);
      setLoading(false);
      setError(null);
      onDocumentLoad?.(pages);
    },
    [onDocumentLoad]
  );

  const handleDocumentLoadError = useCallback((error: Error) => {
    console.error('PDF load error:', error);
    setLoading(false);
    setError('לא ניתן לטעון את המסמך. יתכן שהקובץ פגום או שאין לך הרשאות גישה.');
  }, []);

  const handlePageLoadSuccess = useCallback(() => {
    setPageLoading(false);
  }, []);

  const handlePageLoadError = useCallback(() => {
    setPageLoading(false);
  }, []);

  // Custom text renderer to highlight search/quote
  const textRenderer = useMemo(() => {
    if (!highlightQuote && !searchQuery) return undefined;

    const searchTerms = [highlightQuote, searchQuery].filter(Boolean);

    return (textItem: { str: string }) => {
      let text = textItem.str;

      for (const term of searchTerms) {
        if (!term) continue;
        const regex = new RegExp(`(${escapeRegex(term)})`, 'gi');
        text = text.replace(
          regex,
          '<mark class="bg-yellow-300 text-black px-0.5 rounded">$1</mark>'
        );
      }

      return text;
    };
  }, [highlightQuote, searchQuery]);

  // Loading state
  if (loading) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="text-center">
          <Loader2 className="w-10 h-10 text-blue-500 animate-spin mx-auto" />
          <p className="text-white/60 mt-4">טוען מסמך PDF...</p>
        </div>
      </div>
    );
  }

  // Error state
  if (error) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="text-center max-w-md">
          <FileX className="w-16 h-16 text-red-400 mx-auto" />
          <h3 className="text-white text-lg mt-4">שגיאה בטעינת PDF</h3>
          <p className="text-white/60 mt-2">{error}</p>
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col items-center py-8" dir="ltr">
      <Document
        file={url}
        onLoadSuccess={handleDocumentLoadSuccess}
        onLoadError={handleDocumentLoadError}
        loading={
          <div className="flex items-center justify-center p-12">
            <Loader2 className="w-8 h-8 text-blue-500 animate-spin" />
          </div>
        }
        error={
          <div className="flex items-center justify-center p-12">
            <AlertCircle className="w-8 h-8 text-red-400" />
            <span className="text-white/60 mr-2">שגיאה בטעינת המסמך</span>
          </div>
        }
        className="pdf-document"
      >
        <div className="relative">
          {pageLoading && (
            <div className="absolute inset-0 flex items-center justify-center bg-slate-900/50 z-10">
              <Loader2 className="w-6 h-6 text-blue-500 animate-spin" />
            </div>
          )}
          <Page
            pageNumber={currentPage}
            scale={scale}
            onLoadSuccess={handlePageLoadSuccess}
            onLoadError={handlePageLoadError}
            loading={null}
            renderTextLayer={true}
            renderAnnotationLayer={true}
            className="pdf-page shadow-2xl"
            customTextRenderer={textRenderer}
          />
        </div>
      </Document>

      {/* Page number indicator */}
      <div className="mt-4 text-sm text-white/60">
        עמוד {currentPage} מתוך {numPages}
      </div>
    </div>
  );
};

// Helper to escape regex special characters
function escapeRegex(string: string): string {
  return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
}

export default PDFViewer;
