/**
 * DocumentMinimap - Vertical minimap for page navigation
 *
 * Features:
 * - Shows all pages as small thumbnails
 * - Highlights current page
 * - Marks pages with evidence/contradictions
 * - Virtualized for large documents
 */

import React, { useCallback, useRef, useEffect } from 'react';
import { useVirtualizer } from '@tanstack/react-virtual';
import { AlertTriangle, FileText } from 'lucide-react';

interface DocumentMinimapProps {
  numPages: number;
  currentPage: number;
  onPageClick: (page: number) => void;
  evidencePages?: number[];
  contradictionPages?: number[];
}

export const DocumentMinimap: React.FC<DocumentMinimapProps> = ({
  numPages,
  currentPage,
  onPageClick,
  evidencePages = [],
  contradictionPages = [],
}) => {
  const parentRef = useRef<HTMLDivElement>(null);

  // Create sets for O(1) lookup
  const evidenceSet = new Set(evidencePages);
  const contradictionSet = new Set(contradictionPages);

  // Virtualized list for large documents
  const virtualizer = useVirtualizer({
    count: numPages,
    getScrollElement: () => parentRef.current,
    estimateSize: () => 52, // Height of each page item
    overscan: 10,
  });

  // Scroll current page into view
  useEffect(() => {
    virtualizer.scrollToIndex(currentPage - 1, {
      align: 'center',
      behavior: 'smooth',
    });
  }, [currentPage, virtualizer]);

  const getPageStatus = useCallback(
    (page: number) => {
      if (contradictionSet.has(page)) return 'contradiction';
      if (evidenceSet.has(page)) return 'evidence';
      return 'normal';
    },
    [evidenceSet, contradictionSet]
  );

  return (
    <div
      className="w-20 bg-slate-800 border-r border-slate-700 flex flex-col"
      dir="rtl"
    >
      {/* Header */}
      <div className="px-2 py-2 border-b border-slate-700">
        <div className="text-[10px] font-medium text-white/50 text-center">
          ניווט
        </div>
      </div>

      {/* Legend */}
      <div className="px-2 py-1.5 border-b border-slate-700 space-y-1">
        <div className="flex items-center gap-1 text-[9px] text-white/40">
          <div className="w-2 h-2 rounded-sm bg-amber-400/60" />
          <span>ראיה</span>
        </div>
        <div className="flex items-center gap-1 text-[9px] text-white/40">
          <div className="w-2 h-2 rounded-sm bg-red-400/60" />
          <span>סתירה</span>
        </div>
      </div>

      {/* Pages list */}
      <div
        ref={parentRef}
        className="flex-1 overflow-y-auto"
        style={{ contain: 'strict' }}
      >
        <div
          style={{
            height: `${virtualizer.getTotalSize()}px`,
            width: '100%',
            position: 'relative',
          }}
        >
          {virtualizer.getVirtualItems().map((virtualRow) => {
            const page = virtualRow.index + 1;
            const isCurrent = page === currentPage;
            const status = getPageStatus(page);

            return (
              <div
                key={virtualRow.key}
                style={{
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  width: '100%',
                  height: `${virtualRow.size}px`,
                  transform: `translateY(${virtualRow.start}px)`,
                }}
              >
                <button
                  onClick={() => onPageClick(page)}
                  className={`
                    w-full px-2 py-1.5 flex items-center gap-1.5
                    transition-colors
                    ${
                      isCurrent
                        ? 'bg-blue-600/30 border-r-2 border-blue-500'
                        : 'hover:bg-slate-700/50'
                    }
                  `}
                >
                  {/* Page indicator */}
                  <div
                    className={`
                    w-8 h-8 rounded flex items-center justify-center text-[10px] font-medium
                    ${
                      isCurrent
                        ? 'bg-blue-600 text-white'
                        : status === 'contradiction'
                        ? 'bg-red-900/40 text-red-300 border border-red-500/30'
                        : status === 'evidence'
                        ? 'bg-amber-900/40 text-amber-300 border border-amber-500/30'
                        : 'bg-slate-700/50 text-white/60'
                    }
                  `}
                  >
                    {page}
                  </div>

                  {/* Status icon */}
                  <div className="flex-1 flex justify-end">
                    {status === 'contradiction' && (
                      <AlertTriangle className="w-3 h-3 text-red-400" />
                    )}
                    {status === 'evidence' && (
                      <FileText className="w-3 h-3 text-amber-400" />
                    )}
                  </div>
                </button>
              </div>
            );
          })}
        </div>
      </div>

      {/* Footer with stats */}
      <div className="px-2 py-2 border-t border-slate-700 text-[9px] text-white/40 text-center">
        {numPages} עמודים
      </div>
    </div>
  );
};

export default DocumentMinimap;
